<template>
  <a-scene
    stats
    networked-scene="
      room: dev;
      debug: false;
      adapter: webrtc;
      audio: true;
      serverURL: wss://vrclubbing.herokuapp.com/"
  >
    <!-- load assets for templating-->
    <a-assets timeout="120000">
      <img id="sky" src="sky.jpg" />
      <a-asset-item id="character" src="simple-hoody.glb"></a-asset-item>

      <naftemplate id="avatar-template">
        <a-entity>
          <a-gltf-model id="character" src="#character" position="0 0 0"></a-gltf-model>
          <a-entity networked-audio-source="distanceModel:linear;maxDistance:1;rolloffFactor:10"></a-entity>
        </a-entity>
      </naftemplate>

      <naftemplate id="empty-template">
        <a-entity></a-entity>
      </naftemplate>

      <naftemplate id="left-hand-template">
        <a-entity only-remote-controlled></a-entity>
      </naftemplate>

      <naftemplate id="right-hand-template">
        <a-entity only-remote-controlled></a-entity>
      </naftemplate>
    </a-assets>

    <!-- sky box -->
    <a-sky src="#sky"></a-sky>

    <!-- Lights in scene -->
    <a-entity light="type: ambient; color: #333"></a-entity>
    <a-entity light="type:point;color:#1000ff; intensity:1" position="-10 3 3" sound-reactive-light></a-entity>

    <!-- club geometry -->
    <a-gltf-model src="club2.glb"></a-gltf-model>

    <!-- player and networked players -->
    <a-entity
      id="rig"
      movement-controls="speed:0.05"
      position="0 0 0"
      networked="template:#empty-template;attachTemplateToLocal:false;"
    >
      <a-entity
        camera
        position="0 2.2 0"
        networked="template:#avatar-template;attachTemplateToLocal:false;"
        look-controls="pointerLockEnabled: true"
        listener3d
      ></a-entity>
      <a-entity
        id="leftHand"
        hand-controls-gesture="hand: left; handModelStyle: lowPoly;"
        networked="template:#left-hand-template;attachTemplateToLocal:false"
      ></a-entity>
      <a-entity
        id="rightHand"
        hand-controls-gesture="hand: right; handModelStyle: lowPoly; "
        networked="template:#right-hand-template;attachTemplateToLocal:false"
      ></a-entity>
    </a-entity>
  </a-scene>
</template>

<script>
import AudioRenderer from "../lib/AudioRenderer.js";
import { registerNetworkedComponents } from "../lib/NetworkedComponents.js";
export default {
  name: "Venue",
  props: {
    streamUrl: String
  },

  mounted: function() {
    const { listener, setVolume } = AudioRenderer(window, this.streamUrl);
    setVolume(1);

    window.audioListenerNode = listener;
    window.soundAnalyser = this.analyser;

    registerNetworkedComponents(window.NAF);
  }
};
</script>
