export const registerNetworkedComponents = NAF => {
  NAF.schemas.add({
    template: "#avatar-template",
    components: ["position", "rotation"]
  });

  NAF.schemas.add({
    template: "#empty-template",
    components: ["position", "rotation"]
  });

  NAF.schemas.add({
    template: "#left-hand-template",
    components: ["position", "rotation", "hand-controls-gesture"]
  });

  NAF.schemas.add({
    template: "#right-hand-template",
    components: ["position", "rotation", "hand-controls-gesture"]
  });
};
