export default function(window, streamUrl) {
  const audioCtx = new (window.AudioContext || window.webkitAudioContext)();

  // load audio/radio stream
  const audio = new Audio();
  audio.loop = true;
  audio.autoplay = true;
  audio.crossOrigin = "anonymous";
  audio.src = streamUrl;
  var source = audioCtx.createMediaElementSource(audio);

  // to control loudness / volume
  const gainNode = audioCtx.createGain();

  const minimumGain = audioCtx.createGain();
  minimumGain.gain.value = 0.45;

  // adding impulse response of the room to create a sound that fits the room
  const convolver = audioCtx.createConvolver();

  const splitter = audioCtx.createChannelSplitter(2);

  // adding spatial audio
  const pannerLeft = audioCtx.createPanner();
  pannerLeft.panningModel = "HRTF";
  pannerLeft.distanceModel = "inverse";
  pannerLeft.refDistance = 1;
  pannerLeft.maxDistance = 10000;
  pannerLeft.rolloffFactor = 0.2;
  pannerLeft.coneInnerAngle = 360;
  pannerLeft.coneOuterAngle = 360;
  pannerLeft.coneOuterGain = 0;

  pannerLeft.positionX.setValueAtTime(-10, audioCtx.currentTime);
  pannerLeft.positionY.setValueAtTime(2, audioCtx.currentTime);
  pannerLeft.positionZ.setValueAtTime(5, audioCtx.currentTime);

  pannerLeft.setOrientation(1, 0, 0);

  // adding right channel
  const pannerRight = audioCtx.createPanner();
  pannerRight.panningModel = "HRTF";
  pannerRight.distanceModel = "inverse";
  pannerRight.refDistance = 1;
  pannerRight.maxDistance = 10000;
  pannerRight.rolloffFactor = 0.2;
  pannerRight.coneInnerAngle = 360;
  pannerRight.coneOuterAngle = 360;
  pannerRight.coneOuterGain = 0;

  pannerRight.positionX.setValueAtTime(-10, audioCtx.currentTime);
  pannerRight.positionY.setValueAtTime(2, audioCtx.currentTime);
  pannerRight.positionZ.setValueAtTime(-5, audioCtx.currentTime);

  pannerRight.setOrientation(1, 0, 0);

  const listener = audioCtx.listener;

  const analyser = audioCtx.createAnalyser();
  analyser.fftSize = 32;

  window.soundAnalyserNode = analyser;

  listener.setOrientation(0, 0, -1, 0, 1, 0);

  /** volume between 0 and 1 */
  const setVolume = volume => {
    gainNode.gain.value = volume;
  };

  fetch("factory.hall.wav", {
    mode: "no-cors"
  })
    .then(response => response.arrayBuffer())
    .then(data => {
      audioCtx.decodeAudioData(data, function(buffer) {
        convolver.buffer = buffer;

        convolver.connect(minimumGain);

        source.connect(analyser);
        analyser.connect(gainNode);
        gainNode.connect(convolver);

        convolver.connect(splitter);

        splitter.connect(pannerLeft, 0, 0);
        splitter.connect(pannerRight, 1, 0);

        minimumGain.connect(audioCtx.destination);

        pannerLeft.connect(audioCtx.destination);
        pannerRight.connect(audioCtx.destination);
      });
    })
    .catch(error => console.error(error));

  return {
    listener,
    setVolume
  };
}
